}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

.auth {
  width: min(95%, 60rem);
  margin: 0 auto;
}
.auth__texto {
  text-align: center;
}

.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__fieldset {
  border: none;
  margin-top: 1rem;
  padding: 0;
}
.formulario__legend {
  color: #64748B;
  font-size: 2.2rem;
  margin-bottom: 2rem;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}
.formulario__contenedor-icono {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
}
.formulario__icono {
  background-color: #1E293B;
  width: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.formulario__icono i {
  color: #FFFFFF;
  font-size: 2rem;
}
.formulario__label {
  display: block;
  font-size: 1.8rem;
  color: rgba(26, 27, 21, 0.8);
}
.formulario__input, .formulario__input--sociales, .formulario__select--sociales, .formulario__select {
  padding: 1rem;
  border: 1px solid #394250;
  border-radius: 0.5rem;
  width: 100%;
}
.formulario__input--sociales, .formulario__select--sociales {
  flex: 1;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.formulario__input--file, .formulario__select--file {
  border: none;
}
.formulario__texto {
  margin-top: 3rem;
}
.formulario__radio {
  display: flex;
  gap: 2rem;
}
.formulario__imagen {
  max-width: 20rem;
}
.formulario__imagen img {
  border-radius: 50%;
  border: 1px solid #007df4;
}
.formulario__listado {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.formulario__listado > li {
  cursor: pointer;
  background-color: #007df4;
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: #FFFFFF;
}
.formulario__submit {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
}
.formulario__submit:hover {
  background-color: #0063c1;
  cursor: pointer;
}
.formulario__submit--registrar {
  width: 100%;
  margin-top: 1rem;
}

.acciones {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.acciones__enlace {
  color: #1a1b15;
  font-size: 1.4rem;
  font-weight: 300;
}
.acciones__confirmado {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
}
.acciones__confirmado:hover {
  background-color: #0063c1;
  cursor: pointer;
}

.horas {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .horas {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.horas__hora, .horas__hora--deshabilitada, .horas__hora--seleccionada {
  cursor: pointer;
  color: #007df4;
  border: 1px solid #007df4;
  border-radius: 1rem;
  padding: 1rem;
  text-align: center;
}
.horas__hora--seleccionada {
  background-color: #007df4;
  color: #FFFFFF;
}
.horas__hora--deshabilitada {
  cursor: not-allowed;
  border: 1px solid #64748B;
  color: #64748B;
  opacity: 0.5rem;
}

.header {
  min-height: 60rem;
  background-image: url(../img/grafico.svg), url(../img/grafico.svg), linear-gradient(to right, #1a1b15, rgba(26, 27, 21, 0.5)), image-set(url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
