  flex-direction: column;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table__thead {
  background-color: #1E293B;
  color: #FFFFFF;
}
.table__th {
  padding: 1rem;
  text-align: left;
}
.table__tr:nth-child(even) {
  background-color: #edf2f7;
}
.table__td, .table__td--acciones {
  padding: 1.5rem 1rem;
}
.table__td--acciones {
  display: block;
  justify-content: flex-end;
  gap: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .table__td--acciones {
    display: flex;
  }
}
.table__accion {
  all: unset;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 700;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .table__accion {
    margin: 0;
  }
}
.table__accion--editar {
  color: #007df4;
}
.table__accion--eliminar {
  color: rgb(169, 0, 0);
}

.paginacion {
  margin-top: 5rem;
  margin-bottom: 3rem;
  display: flex;
  gap: 1rem;
  flex-direction: wrap;
  justify-content: center;
  align-items: center;
}
.paginacion__enlace {
  color: #007df4;
  font-size: 2rem;
}
.paginacion__enlace--texto {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 1px solid #007df4;
}
.paginacion__enlace--texto:hover {
  background-color: #007df4;
  color: #FFFFFF;
}
.paginacion__enlace--ultimo, .paginacion__enlace--primero {
  user-select: none;
  padding: 1rem;
  border-radius: 0.5rem;
  border: 1px solid #1E293B;
  color: #1E293B;
  opacity: 0.6;
}
.paginacion__enlace--numero {
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--numero {
    display: inline;
  }
}
.paginacion__enlace--actual {
  user-select: none;
  color: #1E293B;
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--actual {
    display: inline;
  }
}

.listado-ponentes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
.listado-ponentes__ponente, .listado-ponentes__ponente--seleccionado {
  cursor: pointer;
  color: #007df4;
  border: 1px solid #007df4;
  padding: 1rem;
  border-radius: 1rem;
  text-align: center;
}
.listado-ponentes__ponente--seleccionado {
  background-color: #007df4;
  color: #FFFFFF;
}